* {
  box-sizing: border-box;
  margin: 0;
}

body {
  color: #222;
  background: #f9f6f1;
  margin: 0;
  padding: 16px;
  font-family: Arial, sans-serif;
}

.app {
  grid-template-columns: 1fr 320px;
  align-items: start;
  gap: 16px;
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
}

.app-header {
  grid-column: 1 / -1;
  margin-bottom: 16px;
}

.sidebar {
  flex-direction: column;
  align-self: start;
  height: max-content;
  display: flex;
  position: sticky;
  top: 16px;
}

.shopping {
  background: #fff;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 2px 6px #00000080;
}

button {
  transition: background-color .2s, transform .1s, box-shadow .2s;
}

button:hover {
  box-shadow: 0 4px 8px #0003;
}

button:active {
  box-shadow: 0 2px 4px #0003;
}

#print-btn, #clear-btn {
  border: 1px solid #000;
  border-radius: 8px;
  padding: 10px 12px;
}

.toaster {
  z-index: 1000;
  width: 320px;
  position: fixed;
  bottom: 16px;
  right: calc(50% - 550px);
}

.toast {
  color: #fff;
  opacity: .95;
  background: #111;
  border-radius: 8px;
  margin-top: 8px;
  padding: 10px 12px;
}

.small {
  color: #666;
  padding-top: 1rem;
  font-size: .85rem;
}

@media (width <= 1100px) {
  .toaster {
    right: 16px;
  }
}

@media (width <= 800px) {
  .app {
    grid-template-columns: 1fr;
  }

  .toaster {
    width: calc(100vw - 32px);
    max-width: 420px;
    bottom: 16px;
    right: 16px;
  }
}

@media print {
  .main-content, .toaster, .note, .list-item button, .app-header {
    display: none;
  }

  .app {
    max-width: none;
    margin: 0;
    display: block;
  }

  .sidebar {
    width: 100%;
    position: static;
  }

  .shopping {
    box-shadow: none;
    background: #fff;
    border: none;
    width: 100%;
    padding: 0;
    position: static;
  }

  .list {
    border: none;
    max-height: none;
    overflow: visible;
  }
}
